.Educard{
    width: 80%;
    font-weight: bold;
    position: relative;
    animation: mymove 12s infinite;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:white;
    border-radius: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    padding: 0.6rem;
    color:grey
}
@keyframes mymove {
    from {left: 0px;}
    to {left: 10%;}
  }