.Aboutboxelem{
  width: 13rem;
  height: 5rem;
  font-weight: bold;
  position: relative;
  animation: mymove 8s infinite;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color:white;
  border-radius: 10px;
  box-shadow: rgba(68, 47, 47, 0.2) 0px 2px 8px 0px; 
  padding: 1rem;
  margin-top: 0.5rem;
  color:grey
}

.image{
width: 30%;
margin: auto;
background-color: none;
}
.sname{
  font-weight: bold;
}
.scard{
display: grid;
justify-content: center;
align-items: center;
box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; 
padding: 0.5rem;
border-radius: 10%;

}
.aboutbox div:nth-child(1){animation-timing-function: linear;}
 .aboutbox div:nth-child(2){animation-timing-function: ease;}
 .aboutbox div:nth-child(3){animation-timing-function: ease-in;}
 .aboutbox div:nth-child(4){animation-timing-function: ease-out;}
 .aboutbox div:nth-child(5){animation-timing-function: ease-in-out;}

@keyframes mymove {
  from {left: 10%;}
  to {left: 90%;}
}

.skillbox{
  font-weight: bold;
  position: relative;
  animation: mymove 6s infinite;
  justify-content: center;
  align-items: center;
  color:#262525;
  border-radius: 10px;
  padding: 1rem;
  margin-top: 0.5rem;
}
.projectcard{
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    font-weight: bold;
    border-bottom : 1px solid #0a2c50;

}
.btn{
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; 
  background-color: #04192f;
  color:"#262525"
}
.SwiperContaine{
  width: 100%;
}

