/**********************/
/*** HOME CONTAINER ***/
/**********************/


.mt-top-container-home {
	background-image: 
		linear-gradient(
          rgba(0, 0, 0, 0.4),
          rgba(0, 0, 0, 0.8)
          ),
		url("http://joesomma.me/assets/images/background-home.jpg");
	width: 100%;
	background-size: cover;
}

.mt-sub-head-#262525 p {
	color: #262525;
}

.mt-nav-next-#262525 {
	margin-top: 5px;
	text-align: center;
	color: #262525;
}


/**************************/
/*** SERVICES CONTAINER ***/
/**************************/


.mt-container-services {
	background-color: #F5F5F5;
}

.mt-service-thumb-row {
	margin-top: 5px;
}

.mt-service-thumb-row a:hover {
	text-decoration: none;
}

.mt-service-thumb {
	background-color: #F5F5F5;
	border-color: #13456C;
	height: 225px;
}

.mt-service-thumb-text {
	text-align: center;
	color: #777777 !important;
}

.fa-code, .fa-cloud, .fa-puzzle-piece {
	color: #13456C !important;
}

.mt-service-thumb:hover {
	background-color: #262525 !important;
}




.mt-container-portfolio {
	background: 
		linear-gradient(
          rgba(19, 69, 108, 0.4),
          rgba(19, 69, 108, 0.6)
          );
	width: 100%;
	background-size: cover;
}

.mt-portfolio-thumb-row {
	margin-top: 5px;
}

.mt-portfolio-thumb {
	background-color: #13456C;
	box-shadow: 5px 5px 10px #BBBBBB;
}

.mt-portfolio-thumb-text {
	text-align: center;
	color: #262525 !important;
}

.mt-portfolio-thumb:hover {
	background-color: #262525 !important;
}

.mt-portfolio-thumb:hover h4 {
	background-color: #262525 !important;
	color: #13456C !important;
}

